'use strict';
import refs from '../index.js';
import showPromo from './showPromo.js';
import {
  apiCreator,
  createdApi
} from './apiCreator.js';
import preloader from './preloader.js';
import loadMore from './loadMore.js';

const filmSearcher = (event) => {
  event.preventdefault;
  console.log('refs :', refs);
  refs.film_promo.forEach(promo => {
    refs.films_block.removeChild(promo);
  })
  // refs.load_more_btn.addEventListener('click', loadMore);
  let query = refs.film_search_form.firstElementChild.value;
  apiCreator.search(1, query);
  showPromo(createdApi);
  // preloader();
  console.log('refs :', refs);

}

export default filmSearcher;

